.selector-avatar,
.selector-fichas {
	display: flex;
	align-items: center;
	justify-content: space-evenly;
}

.flecha {
	margin: 0px;
	height: 100px;
}

.flecha.prev {
	transform: scaleX(-1);
}

.avatar-container,
.ficha-container {
	margin: 0px;
	height: 150px;
	border: 5px solid #444;
	border-radius: 150px;
	padding: 30px;
}

#avatar,
#avatar-ajustes,
#ficha-ajustes {
	height: 150px;
}
